import React from 'react'
import { Helmet } from "react-helmet"
import favicon from '../../assets/images/favicon.png'

const SEO = () => {
    return (
        <div>
            <Helmet link={[
                {rel: 'shortcut icon', type: 'image/png', href: `${favicon}`}
            ]}>
                <title>Salspot Software - Software Development</title>
                <meta name="description" content="Salspot Software - Software Development"/>
                <meta name="og:title" property="og:title"
                      content="Salspot Software - Software Development"></meta>
                <meta name="twitter:card" content="Salspot Software -Software Development"></meta>
                <link rel="canonical" href="https://salspot-software.com"></link>
                <meta property="og:image" content={favicon}/>
            </Helmet>
        </div>
    )
}

export default SEO
